import { request } from "utils/main";

//  
// keywords type 
// limit  offset 
export function getSearchMsg(keywords, type, limit, offset) {
  return request({
    url: '/cloudsearch',
    params: {
      keywords,
      type,
      limit,
      offset
    }
  })
}
//  keywords 
export function getSearchSug(keywords) {
  return request({
    url: '/search/suggest',
    params: {
      keywords
    }
  })
}