// /
/**
 * id :  id
 * t : 1,
 *  : /follow?id=32953014&t=1
 */
export function follow(id, t) {
  return request({
    url: '',
    params: {
      id,
      t
    }
  })
}

//  name: 
export function createSheet(name) {
  return request({
    url: '/playlist/create',
    params: {
      name
    }
  })
}

// 
export function deleteSheet(id) {
  return request({
    url: '/playlist/delete',
    params: {
      id
    }
  })
}

// / t : ,1:,2: id :  id
export function collectOperate(id, t) {
  return request({
    url: '/playlist/subscribe',
    params: {
      t,
      id
    }
  })
}
/**
 * 
 * 
 :  ,  (  )
 :
op:  add,  del
pid:  id 
tracks:  id,,
 : /playlist/tracks
 : /playlist/tracks?op=add&pid=24381616&tracks=347231 (  '  '  ,  pid , id  tracks  502 )
 */
export function addToSheet(op, pid, tracks) {
  return request({
    url: '/playlist/tracks',
    params: {
      op,
      pid,
      tracks
    }
  })
}