<template>
  <div id="new">
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose">
      <div class="form">
        <span></span>
        <el-input v-model="name"></el-input>
        <p></p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="createSheetFunction(name)"> </el-button>
        <el-button @click="handleClose"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {mapActions, mapMutations} from 'vuex'
export default {
  name: 'CreateSongSheet',

  data() {
    return {
        dialogVisible: true,
        name: '' // 
      };
  },

  mounted() {
    
  },

  methods: {
    ...mapMutations('MySheets', ['hiddenDialog']),
    ...mapActions('MySheets', ['createSheetFunc']),
    createSheetFunction() {
      const id = this.createSheetFunc(this.name)
      this.$emit('closeDialog', id);
      
    },
    handleClose() {
      this.$emit('closeDialog');
    }

  },
};
</script>

<style lang="scss" scoped>
#new {
  /deep/ .el-dialog {
    border-radius: 5px;
    width: 480px;
    height: 249px;
    .el-dialog__header {
      background-color: rgb(45, 45, 45);
      height: 38px;
      line-height: 38px;
      font-weight: 700;
      padding: 0 45px 0 18px;
      border-top-right-radius: 5px;
      border-top-left-radius: 5px;
      .el-dialog__title {
        color: #fff;
        font-size: 14px;
      }
      .el-dialog__close {
        position: relative;
        top: -11px;
      }
    }
    .el-dialog__body {
      padding-bottom: 0;
    }
    .el-dialog__footer {
      text-align: center;
      .el-button {
        padding: 0;
        height: 31px;
        font-size: 12px;
        width: 80px;
      }
    }
  }
  .form {
    text-align: center;
    margin-top: 20px;
    span {
      font-size: 12px;
    }
    p {
      padding-top: 20px;
      font-size: 12px;
      color: #999;
    }
  }
  /deep/ .el-input {
    width: 85%;
  }
}
</style>